import { MovieService } from './../movie.service';
import { MOVIES, Movie } from './../movies';
import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'app-movies',
  templateUrl: './movies.component.html',
  styleUrls: ['./movies.component.css']
})
export class MoviesComponent implements OnInit {

  movies: Movie[];

  constructor(private movieService: MovieService) { }

  ngOnInit() {
     this.getMovies();
     this.movieService.getMoviesAsPromise();
  }

  getMovies() {
    this.movieService.getMovies().subscribe( movies => this.movies = movies);
  }

  delete(movie: Movie) {
    this.movieService.deleteMovie(movie);
    let index = this.movies.indexOf(movie);
    this.movies.splice(index, 1);
    console.log('Movie Deleted In Component');
  }

  add(movieName: string) {
    const movie = { id: 10, name: movieName, type: 'action', rating: 5};
    this.movieService.addMovie(movie);
    this.movies.push(movie);
    console.log('Movie Added In Component');
  }

}
