import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { Movie } from 'src/app/movies';
import { HttpClient } from '@angular/common/http';
import { Promise } from 'q';

@Injectable({
  providedIn: 'root'
})
export class MovieService {
  private url = 'http://localhost:3000/movies';

  constructor(private http: HttpClient) { }


  getMovies(): Observable<any> {
    return this.http.get(this.url);
  }

  getMoviesAsPromise() {
    return this.http.get(this.url).toPromise()
           .then( movies => {
             console.log('movies info: ');
             console.log(movies);
           });
  }
  addMovie(movie: Movie) {
    this.http.post(this.url, movie)
       .subscribe( addedMovie => console.log('movie added successfully'));
  }
  deleteMovie(movie: Movie) {
    this.http.delete( this.url + '/' + movie.id)
       .subscribe( deletedMovie => console.log('movie deleted successfully'));
  }

  updateMovie(movie: Movie): Observable<any> {
    return this.http.put(this.url + '/' + movie.id, movie);
  }



}
