import { MovieService } from './../movie.service';
import { MOVIES } from './../movies';
import { Component, OnInit } from '@angular/core';
import { Movie } from 'src/app/movies';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-movie-detail',
  templateUrl: './movie-detail.component.html',
  styleUrls: ['./movie-detail.component.css']
})
export class MovieDetailComponent implements OnInit {

  movie: Movie;
  constructor( private route: ActivatedRoute, private location: Location, private movieService: MovieService) { }

  ngOnInit() {
    const id = +this.route.snapshot.paramMap.get('id');
    this.movieService.getMovies().subscribe( movies =>  this.movie = movies.filter( movie => movie.id === id)[0]);
  }

  goBack(): void {
    this.location.back();
  }

  save() {
    this.movieService.updateMovie(this.movie).subscribe( updatedMovie => this.goBack());
  }


}
